import React from "react";

const Card = (e) => {
  return (
    <div className={`col-sm-6 col-md-6 col-xxl-${e.col} mb-3`}>
      <div className="card card-custom card-stretch home-widget-section p-3">
        <div className="sk_loader_">
          <div className="wall_progress">
            <p className="sk_line sk_line_height_10 sk_line_80 mb-2"></p>
            <h3 className="sk_line sk_line_40 sk_line_height_26 m-0"></h3>
          </div>
        </div>
      </div>
    </div>
  );
};

const TopcardSkeleton = (props) => {
  const { col, res } = props;
  let cols = Array.from({ length: col }, (_, index) => index);

  return (
    cols &&
    cols.map((col, index) => {
      return <Card key={index} col={res} />;
    })
  );
};

export default React.memo(TopcardSkeleton);
